import React, {useState} from 'react';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const Quiz = () => {
  const [page, setPage] = useState(0);
  const pagesCount = 7;

  return (
    <form className='quiz'>
      {
        (page === 0) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <h2 className='mb-5'>  </h2>
                <p className='mb-5'>          </p>
                <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
              </div>
              <p className='fs-075'>*      </p>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page > 0) &&
        <Row className='gx-5'>
          <Col md={6}>
            <h2> {page}   ?</h2>
          </Col>
          <Col md={6}>
            <div> {page}  {pagesCount}</div>
            <div className='markers'>
              {
                [1, 2, 3, 4, 5, 6, 7].map( item => {
                  return <span key={item} className={(item === page)?'active':''}></span>
                })
              }
            </div>
          </Col>
        </Row>
      }
      {
        (page === 1) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <ul>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>1.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>2.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>3.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>4. </span>
                    </label>
                  </li>
                </ul>
              </div>
              <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page === 2) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <ul>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>1.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>2.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>3.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>4. </span>
                    </label>
                  </li>
                </ul>
              </div>
              <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page === 3) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <ul>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>1.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>2.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>3.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>4. </span>
                    </label>
                  </li>
                </ul>
              </div>
              <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page === 4) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <ul>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>1.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>2.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>3.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>4. </span>
                    </label>
                  </li>
                </ul>
              </div>
              <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page === 5) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <ul>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>1.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>2.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>3.    </span>
                    </label>
                  </li>
                  <li>
                    <label className='pill'>
                      <input type="radio" name={'radio'+page}/>
                      <span>4. </span>
                    </label>
                  </li>
                </ul>
              </div>
              <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
      {
        (page === 6) &&
        <Row md={4} className='gx-5'>
          <Col>
            <label className='figure'>
              <input type="radio" name={'radio'+page} />
              <div className='figure-img' style={{'--img': "url('imgs/img4.jpg')"}}></div>
              <h5 className='text'> </h5>
            </label>
          </Col>
          <Col>
            <label className='figure'>
              <input type="radio" name={'radio'+page} />
              <div className='figure-img' style={{'--img': "url('imgs/img4.jpg')"}}></div>
              <h5 className='text'> </h5>
            </label>
          </Col>
          <Col>
            <label className='figure'>
              <input type="radio" name={'radio'+page} />
              <div className='figure-img' style={{'--img': "url('imgs/img4.jpg')"}}></div>
              <h5 className='text'> </h5>
            </label>
          </Col>
          <Col>
            <label className='figure'>
              <input type="radio" name={'radio'+page} />
              <div className='figure-img' style={{'--img': "url('imgs/img4.jpg')"}}></div>
              <h5 className='text'> </h5>
            </label>
          </Col>
          <Col xs={12}>
            <button type='button' className='btn-2' onClick={()=>setPage(page+1)}></button>
          </Col>
        </Row>
      }
      {
        (page === 7) &&
        <Row className='gx-5'>
          <Col md={6}>
            <div className="h-100 d-flex flex-column justify-content-between">
              <div>
                <input type="text" placeholder='' className='mb-3'/>
                <input type="tel" placeholder='' className='mb-3'/>
                <textarea rows="4" placeholder=''></textarea>
              </div>
              <button type='submit' className='btn-2'></button>
            </div>
          </Col>
          <Col md={6}>
            <img src="imgs/img4.jpg" alt="" className='img'/>
          </Col>
        </Row>
      }
    </form>
  );
};

export default Quiz;